.todo-list-item {
	display: flex;
	align-items: center;

	.todo-list-item-label {
		width: auto;
		flex-grow: 1;
		margin-right: 3px;
		user-select: none;
	}

	.btn {
		width: 30px;
		margin-left: 10px;
	}

	&.important {
		.todo-list-item-label {
			font-weight: bold;
			color: steelblue;
		}
	}

	&.done {
		.todo-list-item-label {
			text-decoration: line-through;
		}
	}
}
